<template>
  <div class="search">
    <nav-bar class="home-nav">
      <div slot="left" class="left" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" />
      </div>
      <div slot="center" class="header">
        <img src="@/styles/img/4、微网站搜索_03.png" />
      </div>
    </nav-bar>

    <div class="search">
      <div class="search-box">
        <input type="text" placeholder="搜索店内精品" v-focus v-model="value" autocomplete="off" />
        <span @click="handleSearch">搜索</span>
        <i class="iconfont icon-sousuo"></i>
      </div>
    </div>

    <!--    历史记录    -->
    <div class="histroy-search">
      <div class="history-title">历史搜索</div>
      <div class="history-title-right" @click="handleclear">清空历史</div>
      <ul class="history-ul" v-for="(item,index) in history" :key="index">
        <router-link :to="{name:'searchResult',query:{value:item}}">{{item}}</router-link>
      </ul>
      <span v-if="show">...</span>
    </div>

    <!-- 内容部分 -->
    <section>
      <goods-list :goods="goods" title="推荐商品"></goods-list>
    </section>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import GoodsList from "components/common/goodsList/GoodsList";
export default {
  name: "Search",
  data() {
    return {
      value: "",
      page: 1,
      pageSize: 6,
      goods: [],
      history: [],
      show: false
    };
  },
  beforeRouteLeave(to, from, next) {
    localStorage.setItem("history", JSON.stringify(this.history));
    next();
  },
  methods: {
    // 清空
    handleclear() {
      this.history = [];
      this.show = false;
    },
    // 点击进入搜索页
    handleSearch() {
      if (this.value == "") {
        this.$toast("请输入搜索的内容");
      } else if(this.value.length >6){
        this.$toast("搜索内容不宜过长");
      }else {
        let index = this.history.indexOf(this.value);
        if (index !== -1) {
          this.history.splice(index, 1);
        }
        this.history.unshift(this.value);

        if (this.history.length > 9) {
          this.history = this.history.slice(0, 9);
          this.show = true;
        }
        this.$router.push({
          name: "searchResult",
          query: { value: this.value }
        });
      }
    },
    async fetchGoodsData() {
      let res = await this.$http.get(this.$api.GOODS, {
        state:1,
        page: this.page,
        pageSize: this.pageSize,
        recommend: 1
      });
      this.goods = res.data.data;
    }
  },
  components: {
    NavBar,
    GoodsList
  },
  created() {},
  mounted() {
    this.fetchGoodsData();
    if (localStorage.getItem("history")) {
      this.history = JSON.parse(localStorage.getItem("history"));
      if (this.history.length === 9) {
        this.show = true;
      }
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/search";
.header img {
  width: 1.94rem;
  height: 0.4rem;
  margin-top: 0.24rem;
}
.left {
  padding-top: 5px;
  padding-left: 5px;
}
section {
  width: 100%;
  height: auto;
  background-color: #f6f6f6;
  margin-top: 0.22rem;
  margin-bottom: 0.98rem;
}
.histroy-search {
  padding: 0 0.24rem;
}

.history-title {
  border-left: 0.03rem solid #ffcb3f;
  padding-left: 0.1rem;
  margin-bottom: 0.2rem;
}
.history-title-right {
  position: absolute;
  right: 0.7rem;
  top: 1.8rem;
}

.histroy-search ul a {
  display: inline-block;
  background-color: #efefef;
  padding: 0.1rem 0.2rem;
  border-radius: 0.5rem;
  font-size: 0.28rem;
  margin-left: 0.4rem;
  margin-bottom: 0.2rem;
  color: black;
  // float: left;
}
.history-ul {
  // height: 30px;
  display: inline;
}
</style>
